// auth.js
import { createPinia, defineStore } from 'pinia';

const pinia = createPinia();

// Active Menu
export const useAppStore = defineStore({
  id: 'app',
  state: () => ({
    activeTab: 'Daftar Jemaat',
  }),
});

// Token
export const useAuthStore = defineStore({
  id: 'auth',
  state: () => ({
    token: null,
  }),
  actions: {
    setToken(token) {
      this.token = token;
      // Simpan token ke dalam cookie atau lakukan logika tambahan
    },
    clearToken() {
      this.token = null;
      // Hapus token dari cookie atau lakukan logika tambahan
    },
    initTokenFromCookie() {
      // Pastikan this ada dan memiliki metode getTokenFromCookie
      if (this && typeof this.getTokenFromCookie === 'function') {
        const token = this.getTokenFromCookie();
        if (token) {
          this.setToken(token);
        }
      }
    },    
  },
  getters: {
    isAuthenticated() {
      return !!this.token;
    },
  },
  methods: {
    getTokenFromCookie() {
      const cookie = document.cookie;
      const tokenStart = cookie.indexOf('token=');

      if (tokenStart !== -1) {
        const tokenEnd = cookie.indexOf(';', tokenStart);
        const token = tokenEnd !== -1 ? cookie.slice(tokenStart + 6, tokenEnd) : cookie.slice(tokenStart + 6);
        return token;
      }

      return null;
    },
  },
});

export { pinia };


