// main.js
import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
//import { createPinia } from 'pinia'; // Import createPinia dari Pinia
import { pinia, useAuthStore } from './stores/auth'

import { initializeApp } from 'firebase/app';
import { firebaseConfig } from './stores/firebaseConfig';

const app = createApp(App);

// Buat instance Pinia dan tambahkan ke aplikasi
//const pinia = createPinia();
app.use(pinia);
initializeApp(firebaseConfig);

const authStore = useAuthStore(pinia);
authStore.initTokenFromCookie();

app.use(router);

app.mount('#app');

